import React from 'react';
import styles from './../Profile.module.css';
import ProfileStatusWithHooks from './ProfileStatus/ProfileStatus.jsx';


const ProfileInfo = (props) => {

  return <div className={styles.info}>

      <span className={styles.full__name}>{props.fullName}</span>
      <span className={styles.full__name}>{props.aboutMe}</span>
      <div className={styles.status}>
        {props.isOwner ? <ProfileStatusWithHooks status={props.status} setProfileStatus={props.setProfileStatus}/> : props.status}
      </div>
      <div>
        <span>{props.lookingForAJob ? ' .' : ' !'}</span>
        <span> {props.lookingForAJobDescription}</span>
      </div>
      <div className={styles.contacts}>
        {props.renderSocialLinks(props.contacts)}
      </div>
      {props.isOwner ? <button onClick={props.activateEditMode}>Edit</button> : ''}
    </div>
}

export default ProfileInfo;
