import { profileAPI } from '../api/api';
import { stopSubmit } from 'redux-form';
import {PhotosType, PostDataType, ProfileType} from "../types/types";

const ADD_POST = 'ADD-POST';
const SET_USER = 'SET-USER-PROFILE';
const SET_USER_STATUS = 'SET-USER-STATUS';
const SET_USER_PHOTO = 'SET-USER-PHOTO';

let initialState = {
  postData: [
    {id: 1, message: "Woof!", likeCount: 5},
    {id: 2, message: "Today am a good boy!", likeCount: 8},
    {id: 3, message: "Where is my tail?", likeCount: 9998}
  ] as Array<PostDataType>,
  profile: null as ProfileType | null,
  status: "",
};

export type InitialStateType = typeof initialState;

const profileReducer = (state = initialState, action: any): InitialStateType => {
  switch (action.type) {
    case ADD_POST:
        let newPost = {
          id: 4,
          message: action.newPost,
          likeCount: 0
      };
      return {
        ...state,
        postData: [...state.postData, newPost]
      };
    case SET_USER:
        return {
          ...state,
          profile: action.profile
        };
    case SET_USER_STATUS:
        return {
          ...state,
          status: action.status
        };
    case SET_USER_PHOTO:
      return {
        ...state,
        profile: {...state.profile, photos: action.photo} as ProfileType
      };
    default: return state;
  }
};

export default profileReducer;

type AddPostActionType = {
  type: typeof ADD_POST,
  newPost: string | null,
};
export const onAddPost = (newPost: string): AddPostActionType => {
  return { type: ADD_POST, newPost };
};
type SetUserProfileActionType = {
  type: typeof SET_USER,
  profile: ProfileType,
};
export const setUserProfile = (profile: ProfileType): SetUserProfileActionType => {
  return { type: SET_USER, profile };
};
type SetUserStatusActionType = {
  type: typeof SET_USER_STATUS,
  status: string | null,
};
export const setUserStatus = (status: string): SetUserStatusActionType => {
  return { type: SET_USER_STATUS, status }
};
type OnSavePhotoActionType = {
  type: typeof SET_USER_PHOTO,
  photo: PhotosType,
}
export const onSavePhoto = (photo: PhotosType): OnSavePhotoActionType => {
  return { type: SET_USER_PHOTO, photo }
};

export const getUserProfile = (id: number) => {
  return (dispatch: any) => {
    profileAPI.getProfile(id).then((data: any) => {
      dispatch(setUserProfile(data));
    })
  }
};
export const getUserStatus = (id: number) => async (dispatch: any) => {
  const data = await profileAPI.getStatus(id);
  dispatch(setUserStatus(data));
};

export const setProfileStatus = (status: string) => async (dispatch: any) => {
  const response = await profileAPI.setStatus(status);
  if (response.resultCode === 0) {
    dispatch(setUserStatus(status));
  }
};

export const savePhoto = (photo: PhotosType) => {
  return (dispatch: any) => {
    profileAPI.updateUserPhoto(photo).then((data: any) => {
      if (data.resultCode === 0) {
        dispatch(onSavePhoto(data.data.photos));
      }
    })
  }
};

export const saveProfile = (profile: any, userId: number) => async (dispatch: any) => {
  const response = await profileAPI.updateUserProfile(profile);
  if (response.data.resultCode === 0) {
    dispatch(getUserProfile(userId));
  } else if (response.data.resultCode === 1) {
    const errors = response.data.messages.map((error: any) => error);
    const errorsObj = errors.reduce((error: any, key: any)  => (
      {...error, [key.slice(30, -1).toLowerCase()]: key.slice(0, 18)}
    ), {});
    dispatch(stopSubmit("edit-profile", {contacts: errorsObj}));
    return Promise.reject(errors[0]);
  }
};
