import { authAPI, securityAPI } from '../api/api';
import { stopSubmit } from 'redux-form';

const AUTH_ME = 'FOLLOW';
const LOGGED_OUT = 'LOGGED_OUT';
const GET_CAPTCHA_URL_SUCCESS = 'GET_CAPTCHA_URL_SUCCESS';

type initialStateType =  typeof initialData;

let initialData = {
  id: null as number | null,
  email: null as string | null,
  login: null as string | null,
  isAuth: false,
  captchaUrl: null as string | null
}

const userAuthMe = (state = initialData, action: any): initialStateType => {
  switch(action.type) {
    case AUTH_ME:
      return {
        ...state,
        ...action.data,
        isAuth: true
      }
    case LOGGED_OUT:
      return {
        ...state,
        id: null,
        email: null,
        login: null,
        isAuth: false
      }
    case GET_CAPTCHA_URL_SUCCESS:
      return {
        ...state,
        ...action.payload
      }
    default: return state;
  }
}

export default userAuthMe;

type SetUserAuthMeActionType = {
  type: typeof AUTH_ME,
  data: {
    id: number | null,
    email: string | null,
    login: string | null,
  }
}
export const setUserAuthMe = (id: number, email: string, login: string): SetUserAuthMeActionType => {
  return {type: AUTH_ME, data: {id, email, login}}
}

type SetUserLoggedOutActionType = {
  type: typeof LOGGED_OUT
}
const setUserLoggedOut = (): SetUserLoggedOutActionType => {
  return {type: LOGGED_OUT}
}

type GetCaptchaUrlSuccessActionType = {
  type: typeof GET_CAPTCHA_URL_SUCCESS,
  payload: {
    captchaUrl: string | null,
  },
}
const getCaptchaUrlSuccess = (captchaUrl: string): GetCaptchaUrlSuccessActionType => {
    return {
      type: GET_CAPTCHA_URL_SUCCESS,
      payload:
        {captchaUrl}
    }
}

export const setMeAuth = () => {
  return (dispatch: any) => {
    return (
      authAPI.getAuthMe().then((data: any) => {
        if (data.resultCode === 0) {
          let {email, id, login} = data.data;
          dispatch(setUserAuthMe(id, email, login));
        }
      })
    )
  }
}

export const setMeLoggedIn = (email: string | null, password: string | null, rememberMe = false, captcha = null) => {
  return (dispatch: any) => {
    authAPI.getLoggedMe(email, password, rememberMe, captcha).then((data: any) => {
      if (data.resultCode === 0) {
        dispatch(setMeAuth());
      } else if (data.resultCode === 1) {
        dispatch(stopSubmit("Login", {_error: "Incorrect email or password."}));
      } else if (data.resultCode === 10) {
        dispatch(stopSubmit("Login", {_error: "You need to enter captcha."}));
        dispatch(getCaptcha());
      }
    })
  }
}

export const setMeLoggedOut = (page: number) => {
  return (dispatch: any) => {
    authAPI.getLoggedOut().then((data: any) => {
      if (data.resultCode === 0) {
        dispatch(setUserLoggedOut());
      }
    })
  }
}

export const getCaptcha = () => async (dispatch: any) => {
  const response = await securityAPI.getCaptcha();
  dispatch(getCaptchaUrlSuccess(response.url));
}
