import React from 'react';
import {reduxForm, Field, FormSection} from 'redux-form';
import {Input, Textarea} from './../../Shared/Textarea/Textarea.jsx';
import {required} from './../../Shared/Validator.js';
import stylesError from './../../Login/Login.module.css';

const EditProfileForm = (props) => {
  const { handleSubmit } = props;

  const contactsInputs = (contacts) => {
    return Object.keys(contacts).map(key => <div key={key}>
                  {key}: <Field component={Input} type="text" name={key} placeholder={key}></Field>
              </div>
        )
  }

  return <form onSubmit={handleSubmit}>
      {props.error ? <span className={stylesError.commonError}>{props.error}</span> : ""}
      <Field name="fullName" validate={required} component={Input} placeholder="Full name" type="text"></Field>
      <Field name="aboutMe" validate={required} component={Textarea} placeholder="About Me" type="text"></Field>
      <label>looking for a job: </label>
      <Field name="lookingForAJob" component={Input} type="checkbox"></Field>
      <Field name="lookingForAJobDescription" validate={required} component={Textarea} placeholder="Job description" type="text"></Field>
      <FormSection name="contacts">
        {contactsInputs(props.profile.contacts)}
      </FormSection>


    <button>Save</button>
  </form>
}

const ReduxEditProfileForm = reduxForm({
  form: 'edit-profile'
})(EditProfileForm)

export default ReduxEditProfileForm;
