import React, {useState, useEffect} from 'react';

const ProfileStatusWithHooks = (props) => {

  let [editMode, setEditMode] = useState(false);
  let [status, setStatus] = useState(props.status);

  const activateEditMode = () => {
    setEditMode(true);
  }

  const deActivateEditMode = () => {
    setEditMode(false);
    props.setProfileStatus(status);
  }

  const updateStatusText = (e) => {
    setStatus(e.currentTarget.value);
  }

  useEffect( () => {
    setStatus(props.status);
  }, [props.status])

  if (!editMode) {
    return <span onDoubleClick={activateEditMode}>{props.status || '----'}</span>
  } else if (editMode) {
    return <input autoFocus={true} onChange={updateStatusText} onBlur={deActivateEditMode} value={status} ></input>
  }
}

export default ProfileStatusWithHooks;
