import { usersAPI } from '../api/api';
import { UsersType } from "../types/types";

const FOLLOW = 'FOLLOW';
const UNFOLLOW = 'UNFOLLOW';
const SET_USERS = 'SET_USERS';
const USERS_COUNT = 'USERS_COUNT';
const CURRENT_PAGE = 'CURRENT_PAGE';
const TOGGLE_IS_FETCHING = 'TOGGLE_IS_FETCHING';
const FOLLOWING_IN_PROGRESS = 'FOLLOWING_IN_PROGRESS';


type InitialStateType = typeof initialData;

let initialData = {
  usersCount: 0,
  currentPage: 1,
  usersPerPage: 10,
  users: [] as Array<UsersType>,
  isFetching: false,
  usersInFollowingProgress: [] as Array<number>,
}

const usersReducer = (state = initialData, action: any): InitialStateType => {
  switch(action.type) {
    case FOLLOW:
      return {
        ...state,
        users: state.users.map(user => {
          if(user.id === action.id) {
            return {
              ...user,
              followed: true
            }
          }
          return user;
        }) as Array<UsersType>
      }
    case UNFOLLOW:
      return {
        ...state,
        users: state.users.map(user => {
          if (user.id === action.id) {
            return {
              ...user,
              followed: false
            }
          }
          return user;
        }) as Array<UsersType>
      }
    case SET_USERS:
      return {
        ...state,
        users: [...action.users]
      }
    case USERS_COUNT:
      return {
        ...state,
        usersCount: action.usersCount
      }
    case CURRENT_PAGE:
      return {
        ...state,
        currentPage: action.page
      }
    case TOGGLE_IS_FETCHING:
      return {
        ...state,
        isFetching: action.toggle
      }
    case FOLLOWING_IN_PROGRESS:
      return {
        ...state,
        usersInFollowingProgress: action.isFetching
            ? [...state.usersInFollowingProgress, action.userID]
            : state.usersInFollowingProgress.filter((id) => id !== action.userID)
      }
    default: return state;
  }
}

type FollowUserActionType = {
  type: typeof FOLLOW,
  id: number,
}
const followUser = (userID: number): FollowUserActionType => {
    return {type: FOLLOW, id: userID}
}

type UnFollowUserActionType = {
  type: typeof UNFOLLOW,
  id: number,
}
const unFollowUser = (userID: number): UnFollowUserActionType => {
    return {type: UNFOLLOW, id: userID}
}

type setUsersActionType = {
  type: typeof SET_USERS,
  users: Array<UsersType>,
}
const setUsers = (users: Array<UsersType>): setUsersActionType => {
    return {type: SET_USERS, users}
}

type SetUsersCountActionType = {
  type: typeof USERS_COUNT,
  usersCount: number,
}
const setUsersCount = (usersCount: number): SetUsersCountActionType => {
    return {type: USERS_COUNT, usersCount}
}

type SetUserInFollowingProgressActionType = {
  type: typeof FOLLOWING_IN_PROGRESS,
  isFetching: boolean,
  userID: number,
}
const setUserInFollowingProgress = (isFetching: boolean, userID: number): SetUserInFollowingProgressActionType => {
    return {type: FOLLOWING_IN_PROGRESS, isFetching, userID}
}

type SetCurrentPageActionType = {
  type: typeof CURRENT_PAGE,
  page: number,
}
export const setCurrentPage = (page: number): SetCurrentPageActionType => {
    return {type: CURRENT_PAGE, page}
}

type SetToggleIsFetchingActionType = {
  type: typeof TOGGLE_IS_FETCHING,
  toggle: boolean,
}
export const setToggleIsFetching = (toggle: boolean): SetToggleIsFetchingActionType => {
    return {type: TOGGLE_IS_FETCHING, toggle}
}

export const getUsers = (currentPage: number, usersPerPage: number) => {
  return (dispatch: any) => {
    dispatch(setToggleIsFetching(true));
    usersAPI.getUsers(currentPage, usersPerPage).then((data: any) => {
        dispatch(setToggleIsFetching(false));
        dispatch(setUsers(data.items));
        dispatch(setUsersCount(data.totalCount));
    })
  }
}
export const onFollow = (id: number) => {
  return (dispatch: any) => {
    dispatch(setUserInFollowingProgress(true, id));
    usersAPI.followUser(id).then((data: any) => {
      if (data.resultCode === 0) {
        dispatch(followUser(id));
      }
      dispatch(setUserInFollowingProgress(false, id));
    })
  }
}

export const onUnfollow = (id: number) => {
  return (dispatch: any) => {
    dispatch(setUserInFollowingProgress(true, id));
    usersAPI.unfollowUser(id).then((data: any) => {
      if (data.resultCode === 0) {
        dispatch(unFollowUser(id));
      }
      dispatch(setUserInFollowingProgress(false, id));
    })
  }
}

export default usersReducer;
