import React from 'react';
import styles from './Profile.module.css';
import PostsContainer from './Posts/PostsContainer.jsx';
import Profile from './Profile/Profile.jsx';
import {connect} from 'react-redux';
import {getUserProfile, getUserStatus, setProfileStatus, savePhoto, saveProfile} from '../../redux/profile-reducer.ts'
import {withRouter} from 'react-router-dom';
import withAuthRedirect from './../../hoc/withAuthRedirect.js';
import {compose} from 'redux';

class ProfileAPI extends React.Component {
    refreshProfile () {
      let userID = this.props.match.params.userId;
      if (!userID) {
        if (this.props.isAuth) {
          userID = this.props.loggedUser;
          this.props.getUserProfile(userID);
          this.props.getUserStatus(userID);
        }
      } else {
          this.props.getUserProfile(userID);
          this.props.getUserStatus(userID);
        }
    }

    componentDidMount() {
      this.refreshProfile();
    }

    componentDidUpdate(prevProps, prevState) {
      if (this.props.match.params.userId !== prevProps.match.params.userId) {
        this.refreshProfile();
      }
    }

    render = () => {
      return <div className={styles.content}>
            <Profile {...this.props.profileData.profile}
              profile={this.props.profileData.profile}
              isOwner={!this.props.match.params.userId}
              savePhoto={this.props.savePhoto}
              status={this.props.profileData.status}
              setProfileStatus={this.props.setProfileStatus}
              getUserStatus={this.props.getUserStatus}
              saveProfile={this.props.saveProfile}
              />
            <PostsContainer
              store={this.props.store}
              dispatch={this.props.dispatch}
            />
        </div>
    }
}

let mapStateToProps = (state) => {
    return {
        profileData: state.profileData,
        isAuth: state.authMe.isAuth,
        loggedUser: state.authMe.id
    }
}

export default compose(
  withRouter,
  connect(mapStateToProps, {getUserProfile, getUserStatus, setProfileStatus, savePhoto, saveProfile}),
  withAuthRedirect
)(ProfileAPI);;
