import { setMeAuth } from './auth-reducer';

const INITILIZE_APP = 'INITILIZE_APP';

let initialData = { initializied: false };

type InitialDataType = typeof initialData;

const appReducer = (state = initialData, action: any): InitialDataType => {
  switch(action.type) {
    case INITILIZE_APP:
      return {
        ...state,
        initializied: true,
      };
    default: return state;
  }
};

type SetAppInitializeActionType = {
  type: typeof INITILIZE_APP,
};

export const setAppInitialize = (): SetAppInitializeActionType => {
  return { type: INITILIZE_APP };
};

export const setAppInitializeThunkCreator = () => (dispatch: any) => {
  let promise = dispatch(setMeAuth());
  Promise.all([promise]).then(() => {
    dispatch(setAppInitialize());
  });
};

export default appReducer;