import React from 'react';
import styles from './Login.module.css';
import {Field} from 'redux-form';
import {required, maxLengthCreator, minLength8} from '../Shared/Validator';
import {Input} from '../Shared/Textarea/Textarea';

const maxLength20 = maxLengthCreator(20);

const LoginForm = (props) => {
  const captchaUrl = props.captchaUrl;
    return (
      <form onSubmit={props.submit} className={styles.login__form}>
          <Field name="email" validate={[required, maxLength20]} component={Input} placeholder="Email" type="text"/>
          <Field name="password" validate={[required, minLength8]} component={Input} placeholder="Password" type="password"/>
          <span><Field name="rememberMe" component={Input} type="checkbox"/> Remember me.</span>
          {captchaUrl && <img className={styles.captcha} src={captchaUrl} alt="captcha"/>}
          {captchaUrl && <Field name="captcha" validate={[required]} component={Input} placeholder="Enter the captcha" type="text"/>}
          <button type="submit">Submit</button>
          {props.error ? <span className={styles.commonError}>{props.error}</span> : ""}
        </form>
    )
}

export default LoginForm;
