import React, {useState} from 'react';
import styles from './../Profile.module.css';
import banner from './../../../assets/images/banner.jpg';
import Preloader from './../../Shared/Preloader.jsx';
import ProfileInfo from './ProfileInfo.jsx';
import EditProfileForm from './EditProfileInfo.jsx';
import userPhoto from './../../../assets/images/user.png';

const Profile = (props) => {

  let [editMode, setEditMode] = useState(false);

  const activateEditMode = () => {
    setEditMode(true);
  }

  const deActivateEditMode = () => {
    setEditMode(false);
  }

  const getSavedPhoto = (e) => {
    if (e.target.files.length > 0) {
      props.savePhoto(e.target.files[0]);
    }
  }

  const renderSocialLinks = (contacts) => {
    return (
      Object.entries(contacts).map(([caption, url]) => {
          if (!url) {
            return '';
          }
          const newUrl = /^http/.test(url) ? url : 'https://' + url;
          return <span key={caption}>
            {caption}: <a target="_new" href={newUrl}>Link</a>
          </span>
      })
    )
  }

  const onSubmit = (profile) => {
      props.saveProfile(profile, props.userId).then(
        () => {
          deActivateEditMode();
        }
      );
  }

  if (!props.userId) {
    return  <Preloader />
  }
  return  <div className={styles.profileWrapper}>
      <div>
        <img className={styles.banner} src={banner} alt="User banner"/>
      </div>
      <div className={styles.info}>
        <img className={styles.profile__image} src={!props.photos.large ? userPhoto : props.photos.large} alt={props.fullName + "'s avatar"} />
        {props.isOwner ? <input type="file" className={styles.image__upload} onChange={getSavedPhoto}/> : "" }
        {editMode
          ? <EditProfileForm initialValues={props.profile} profile={props.profile} onSubmit={onSubmit}/>
          : <ProfileInfo {...props.profile}
          renderSocialLinks={renderSocialLinks}
          isOwner={props.isOwner}
          savePhoto={props.savePhoto}
          status={props.status}
          setProfileStatus={props.setProfileStatus}
          getUserStatus={props.getUserStatus}
          activateEditMode={activateEditMode} />
        }
      </div>
    </div>
}

export default Profile;
