const addMessage = 'ADD-MESSAGE';

type DialogDataType = {
  id: number,
  name: string,
  avatar: string,
}

type MessageDataType = {
  id: number,
  message: string,
  direction: string,
}

let initialData = {
  dialogData: [
    {id: 1, name: "Sharik", avatar: 'https://source.unsplash.com/random/50x50/?random=1'},
    {id: 2, name: "Popugai Ivan", avatar: 'https://source.unsplash.com/random/50x50/?random=2'}
  ] as Array<DialogDataType>,
  messageData: [
    {id: 1, message: "Zdarova!", direction: "in"},
    {id: 2, message: "Kuda propal?", direction: "in"},
    {id: 3, message: "Go v basik?!", direction: "in"},
    {id: 4, message: "Zavtra Go", direction: "out"}
  ] as Array <MessageDataType>
};

type InitialDataType = typeof initialData

const dialogReducer = (state = initialData, action: any): InitialDataType => {
  switch (action.type) {
    case addMessage:
      let newMessage = {
        id: 5,
        message: action.newMessage,
        direction: 'out'
      }
      return {
        ...state,
        messageData: [...state.messageData, newMessage]
      }
    default: return state;
  }
}

export default dialogReducer;

type AddNewMessageActionType = {
  type: typeof addMessage,
  newMessage: string,
}
export const addNewMessage = (newMessage: string): AddNewMessageActionType => {
  return {type: addMessage, newMessage};
}
